<template>
  <div>
      
      <!-- 面包屑 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>数据统计</el-breadcrumb-item>
          <el-breadcrumb-item>数据报表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 卡片视图 -->
      <el-card>

             <!-- 2.为 ECharts 准备一个具备大小（宽高）的 DOM -->
            <div id="main" style="width: 800px;height:400px;"></div>

      </el-card>
  </div>
</template>
<script>
//1.导入echarts
import echarts from 'echarts'
import _ from 'lodash'
export default {
  data(){
      return {
          //需要合并的数据
    options: {
        title: {
          text: '用户来源'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#E9EEF3'
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ]
      }
      }
  },
  created(){

  },
 async mounted(){//此时页面上的dom元素已经被渲染完毕了 也就是到执行到mounted时 页面的dom结构刚好渲染处理             
        // 3.基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('main'));

            var {data:res} = await this.$http.get('reports/type/1')
                if(res.meta.status !==200) return this.$message.error('获取折线图数据失败')


        //4.z准备数据和配置项
         // 指定图表的配置项和数据
           var result =  _.merge(res.data,this.options)//这一步是将括号中的2个对象合并

        //5.展示数据需要调用mychart身上的一个函数setOption
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(result);
  },
  methods:{

  }
}
</script>
<style scoped>

</style>
